.card-product {
  @include poppins;

  @media (max-width: $mobile-max-width) {
    width: 130px;
  }

  img {
    margin-bottom: 13px;
    width: 270px;
    height: 284px;

    @media (max-width: $tablet-max-width) {
      margin-bottom: 16px;
      width: 324px;
      height: 340px;
    }

    @media (max-width: $mobile-max-width) {
      margin-bottom: 10px;
      width: 130px;
      height: 136px;
    }

  }

  h3 {
    margin-top: 0;
    margin-bottom: 5px;
    font-weight: 500;
    font-size: 16px;
    line-height: 130%;
    text-align: center;
  }

  p {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: bold;
    font-size: 14px;
    line-height: 130%;
    text-align: center;
  }

  a {
  color: inherit;

  &:visited {
    color:inherit;
  }

  &:hover {
    opacity: 0.5;
    color: rgba($black-rgb, 0.5);
  }

  &:active {
    opacity: 0.2;
  }
  }
}
